import { useState } from "react";

function ArraysinHooks() {

    const [inputVal, setInputVal] = useState(0)
    const [myArray, setArrayValues] = useState(["a", "b", "c", "d"])


    const consoleVal = () => {
        console.log("The value in the arrya", inputVal)
        console.log("The value in the arrya () ", setInputVal)
    }

    return (
        <>
            <input type="text"
                value={inputVal}
                onChange={e => setInputVal({ ...inputVal, inputVal: e.target.value })}

            />
            <button onClick={consoleVal}> Submit </button>
            <div> The items in the array are </div>
        </>
    )

}

export default ArraysinHooks;